import React, {Component} from 'react';
import {
    ImageTemplate1, 
    VideoTemplate1,
    BrandTemplate1
} from './templates';

class GoodsViewDetail extends Component{

    state = {
        section: [
            {
                key: 0,
                type: 'brand1',
                goodsNo: '1000000000',
                brandNm: 'nomad',
                article: `2012   Nomad ,         . 
                        Nomad                . 
                        Nomad        ,       .`
            },
            {
                key: 1,
                type: 'video1',
                goodsNo: '1000000000',
                videoNm: 'base_station'
            },
            {
                key: 2,
                type: 'image1',
                goodsNo: '1000000000',
                title: 'Apple Watch Edition',
                article: '              ( 3 )',
                imageNm: [
                    '1-1.jpg'
                ]
            },
            {
                key: 3,
                type: 'image1',
                goodsNo: '1000000000',
                title: 'Apple Watch Edition',
                article: '              ( 3 )',
                imageNm: [
                    '1-2.jpg'
                ]
            }
        ]
    }

    
  

    render(){

        const {isMobile} = this.props;

        const {section} = this.state;

        let sections = section.map((value, index) =>{
            switch(value.type){
                case 'brand1':
                    return (<BrandTemplate1 value={value} key={value.key} />);
                case 'image1':
                    return (<ImageTemplate1 value={value} isMobile={isMobile} key={value.key} />);
                case 'video1':
                    return (<VideoTemplate1 value={value} key={value.key} />)
                default:
                    return (' ');
            }
        });

        return (
            <div className="goodsview__middle-info-wrapper">
                <div className="goodsview__lower-info">
                    <div className="goodsview__detail">
                        {sections}
                    </div>
                </div>
            </div>
        );  
    }
}

export default GoodsViewDetail;