import React, {Component} from 'react';

import Slider from "react-slick";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";

class GoodsViewTop extends Component{

    buyButton = null;
    selectOption = null;

    static defaultProps = {
        backgroundColor: '#f8f8f8'
    }

    componentDidMount(){
        const {updateBuyButtonDom} = this.props;
        updateBuyButtonDom(this.buyButton);
    }

    domToArray(dom, length){
        let array = [];
        
        for(let i=0; i<length; i++){
            array.push(dom[i]);
        }

        return array;
    }

    handleOptionSelect(e){
        // console.log('e', e.currentTarget);
        e.currentTarget.classList.toggle('open');

        let optionDom = e.currentTarget.children;
        let optionLength = e.currentTarget.children.length;
        let optionArray = this.domToArray(optionDom, optionLength);

        optionArray.forEach((val, index) => {
            val.classList.remove('on');
        });

        e.target.classList.add('on');
        this.selectOption.value = e.target.dataset.value;
    }

    render(){
        const {backgroundColor, toggleShowBuyInfo} = this.props;
        const settings = {
            dots: true,
            infinite: true,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1,
            arrows: false,
            autoplay: true,
            autoplaySpeed: 2500
        };



        return(
            <div className="goodsview__upper-info-wrapper" style={{backgroundColor: backgroundColor}}>
                <div className="goodsview__upper-info">
                    <div className="goodsview__upper-info-thumbnail">
                        <Slider {...settings}>
                            <img src={require(`../../../assets/images/goodsview/1000000000/thumbnails/aw_1.jpg`)} alt=""/>
                            <img src={require(`../../../assets/images/goodsview/1000000000/thumbnails/aw_2.jpg`)} alt=""/>
                            <img src={require(`../../../assets/images/goodsview/1000000000/thumbnails/aw_3.jpg`)} alt=""/>
                            <img src={require(`../../../assets/images/goodsview/1000000000/thumbnails/aw_4.jpg`)} alt=""/>
                        </Slider>
                    </div>
                    <div className="goodsview__upper-info-text">
                        <div className="goodsview__upper-info-logo-likebtn">
                            <div><i className="icon-nomad_logo"></i></div>
                            <div className="goodsview__upper-info-likebtn"><img src={require(`../../../assets/images/common/heart_grey.png`)} alt=""/></div>
                        </div>
                        <div className="goodsview__upper-info-title">
                            <h3>Base Station ()</h3>
                        </div>
                        <div className="goodsview__upper-info-subtitle">, , ,  </div>
                        <div className="goodsview__upper-info-thumbnail--mobile">
                            <Slider {...settings}>
                                <img src={require(`../../../assets/images/goodsview/1000000000/thumbnails/aw_1.jpg`)} alt=""/>
                                <img src={require(`../../../assets/images/goodsview/1000000000/thumbnails/aw_2.jpg`)} alt=""/>
                                <img src={require(`../../../assets/images/goodsview/1000000000/thumbnails/aw_3.jpg`)} alt=""/>
                                <img src={require(`../../../assets/images/goodsview/1000000000/thumbnails/aw_4.jpg`)} alt=""/>
                            </Slider>
                        </div>
                        <div className="goodsview__upper-info-price-mileage">
                            <div className="goodsview__upper-info-price">20,500</div>
                            <div className="goodsview__upper-info-mileage"> 4100</div>
                        </div>
                        <div className="goodsview__upper-info-detail-text">
                                     
                                       . 
                                         . 
                            ( )
                        </div>
                        <div className="goodsview__upper-info-option-buybutton" ref={ref => {this.buyButton = ref}}>
                            <div className="goodsview__upper-info-option-wrapper">
                                <select className="goodsview__upper-info-select-option mobile-only" ref={ref=>{this.selectOption = ref}}>
                                    <option> </option>
                                    <option value="basic">Basic Edition</option>
                                    <option value="apple">Apple Edition</option>
                                </select>
                                <div className="goodsview__upper-info-option desktop-only" onClick={(e) => this.handleOptionSelect(e)}>
                                    <div className="default on"> </div>
                                    <div data-value="basic">Basic Edition</div>
                                    <div data-value="apple">Apple Edition</div>
                                </div>
                            </div>
                            <div className="goodsview__upper-info-buybutton"></div>
                        </div>
                        <div className="goodsview__upper-info-fake-buybutton" onClick={toggleShowBuyInfo}>
                            
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default GoodsViewTop;